export let OrderPaymentForm = {
    name: 'OrderPayment',
    data() {
        return {
            lastName: '',
            name: '',
            secondName: '',
            orderCreateUrl: 'sber:payment.ordercontroller'
        }
    },
    methods: {
        createOrder() {
            let formSendAll = new FormData();

            formSendAll.append('last_name', this.lastName);
            formSendAll.append('name', this.name);
            formSendAll.append('second_name', this.secondName);


            this.$request
                .setUrl(this.orderCreateUrl + 'create')
                .setData(formSendAll)
                .send()
                .then(response => {
                    console.log(response);
                });
        }
    }
}
export default {OrderPaymentForm}
